('/*\nCopyright DEPP © 2017 - Ministère de l\'éducation nationale  \n*/\n\n\n/*add some style, need to scope (using the typeIdentifier as class prefix) every style declaration to prevent collision */\n\n.lentillesInteraction {\n    font-size: unset;\n    text-align: left;\n    background: #fff;\n    font-weight: 100;\n    color: black;\n}\n\n.qti-customInteraction .lentilles {\n    margin: 10px;\n    max-width: 1000px;\n}\n\n.qti-customInteraction .lentilles .hiddenrep {\n    display: none;\n}\n\n.qti-customInteraction .lentilles .zonefdc {\n    padding: 5px;\n    text-align: center;\n}\n\n.qti-customInteraction .lentilles .lentils {\n    text-align: center;\n    border: 1px grey solid;\n}\n\n.qti-customInteraction .lentilles .lentils img {\n    width: 900px;\n}\n\n.qti-customInteraction .lentilles .consigne {\n    font-size: 15px;\n    padding: 10px;\n}\n\n.qti-customInteraction .lentilles table,\nth,\ntd {\n    border: 1px solid black;\n    border-collapse: collapse;\n}\n\n.qti-customInteraction .lentilles .coin {\n    width: 50px;\n    border: 1px solid black;\n}\n\n.qti-customInteraction .lentilles .entete_lettre {\n    width: 100px;\n    text-align: center;\n    border: 1px solid black;\n    background-color: lightgrey;\n}\n\n.qti-customInteraction .lentilles .entete_nb {\n    /* width: 50px; */\n    text-align: center;\n    border: 1px solid black;\n    background-color: lightgrey;\n}\n\n.qti-customInteraction .lentilles .cellbase {\n    /*  width: 100px; */\n    text-align: center;\n    border: 1px solid lightgrey;\n    background-color: white;\n}\n\n.qti-customInteraction .lentilles svg {\n    margin-top: 10px;\n    border: 1px grey solid;\n}\n\n.qti-customInteraction .lentilles .graphor {\n    opacity: 0;\n    text-align: center;\n}\n\n.qti-customInteraction .lentilles .etape {\n    font-weight: bold;\n    font-size: 20px;\n}\n\n.qti-customInteraction .lentilles .consignetableau {\n    font-size: 12px;\n}\n\n.qti-customInteraction .lentilles .tablor {\n    text-align: center;\n}\n\n.qti-customInteraction .lentilles .btgraphor {\n    cursor: pointer;\n    line-height: 1;\n    padding: 10px;\n    margin: 10px;\n    font-size: 20px;\n    font-weight: bold;\n}\n\n.qti-customInteraction .lentilles .retourEtape1 {\n    line-height: 1;\n    cursor: pointer;\n    padding: 10px;\n    margin: 10px;\n    font-size: 20px;\n    font-weight: bold;\n}\n\n.qti-customInteraction .lentilles .etape1 {\n    color: blue;\n    cursor: pointer;\n}\n\n.qti-customInteraction .lentilles .etape2 {\n    color: blue;\n    cursor: pointer\n}');
